<template>
  <div id="app">
      

    <router-view/>
  </div>
</template>
<script>

/* firabase */
import { getFirestore } from 'firebase/firestore'
import { mapActions } from 'vuex'

export default {
  name:'App',
  methods:{
    ...mapActions(["instanceFirestore", "getMovieList" , "getNick"]),
    initFirestore() {
      this.instanceFirestore(getFirestore);
    },

  },
  created(){
    this.initFirestore();
  },
  beforeMount() {
    this.getMovieList();
    this.getNick();
    
  },
}
</script>


